                                                <a class="update actions-onclick" v-if="ModuleNameAll !='HR'" data-toggle="modal" v-on:click="RequestTimeBtn(item)" href="javascript:;" v-bind:class="(item.IS_APPROVE_REJECT > 0 || item.STATUS.toLowerCase()=='completed' ||(['ACT','REFPEN','PNPGRS'].indexOf(item.PROJECT_STATUS_ID)==-1 && item.LINKED_SOURCE_NAME==null) ?'edit-list disabled_icon':'edit-list')">
