                //this.ProjectId = item.PROJECT_ID;
                this.showAddNoteModal = (this.showAddNoteModal == true) ? false : true;
                this.header = this.$t('AddNotes');
                // }
            }
        },
        ProgressDivOpen: function (item) {
            //debugger;
            var vm = this;
            vm.showUpdateProgressModal = true;
            item.StopTimerBtn = true;
            item.UpdateProgress = true;
            vm.TRACKED_HOURS = this.convertSecondsToHHMMSSNew(item.TRACKED_HOURS);
            vm.TASK_ID = item.TASK_ID;
            vm.PROGRESS = item.PROGRESS;
        },
        trackLocation: function (trackId) {
            this.TrackId = trackId;
            this.showTrackLocationModal = true;
        },
        FormatDateToTime(Getdate, formattype) {
            return moment(Getdate).format(formattype);
        }
    }
}
