                                        </div>
                                    </div>
                                    <span class="t-title">{{ $t('TaskProgress') }}</span>
                                </div>
                            </div>
                            <div class="spac-eq border-top text-center">
                                <!-- v-model="item.TotalTime"-->
                                <div id="TotalTime" v-if="item.AutomaticBtn" class="timerbx green"></div>
                                <div v-if="item.RUNNING_STATUS=='PAUS' || item.RUNNING_STATUS=='ACT'">

                                    <button v-if="store.getTimer.statusCode=='ACT'" v-on:click="timerStopOrPause('pause','TimeTrack')" type="button" class="btn Timertodo btn-info">
                                        <span class="tag-name">{{$t('Pause')}}</span>
                                    </button>
                                    <button v-if="store.getTimer.statusCode=='PAUS'" v-on:click="timerStopOrPause('resume','TimeTrack')" type="button" class="btn Timer btn-success">
                                        <span class="tag-name">{{$t('Resume')}}</span>
                                    </button>
                                    <button v-if="item.RUNNING_STATUS=='PAUS' || item.RUNNING_STATUS=='ACT'" v-on:click="timerStopOrPause('stop','TimeTrack')" type="button" class="btn Timer btn-danger">
                                        <span class="tag-name">{{$t('Stop')}}</span>
                                    </button>
                                </div>
                                <div v-else>
                                    <button :disabled="store.getTimer.trackAgent=='silentapp' ? true : false" type="button" v-bind:class="(item.STATUS.toLowerCase()=='completed'?'btn Timer manually-time inactive-disabled':'btn Timer manually-time')" v-on:click="manualyAddTime(item)" data-toggle="modal" href="javascript:;">
                                        <span class="tag-name"><em class="fa fa-clock-o mr-1"></em>{{ $t('AddManually') }}</span>
                                    </button>
                                    <button :disabled="store.getTimer.trackAgent=='silentapp' ? true : false" type="button" v-bind:class="(item.STATUS.toLowerCase()=='completed'?'btn Timer auto-time inactive-disabled':'btn Timer auto-time')" v-on:click="automaticStart(item)" data-toggle="modal" href="javascript:;">
                                        <span class="tag-name"><em class="fa fa-history mr-1"></em>{{$t('Automatic')}}</span>
                                    </button>
                                </div>
                            </div>
                            <div class="verticle-action mt-1">
                                <em class="fa fa-ellipsis-v action_icon_cv" aria-hidden="true" style="width:10px;"></em>
                                <span class="action-list-box small">
                                    <span class="list-actions fsm-list" id="action-list">
                                        <div class="float-left">
                                            <div class="float-left">
                                                <a data-toggle="modal" v-on:click="ProgressDivOpen(item)" v-if="item.PROJECTTASKPROGRESS=='MANUALLY_BASED'" checkPrivilege="yes" href="javascript:;" data-placement="left" style="cursor:pointer;" title="Update Progress" v-bind:class="(item.STATUS.toLowerCase()=='completed' ?'update actions-onclick disabled_icon':'update actions-onclick')">
                                                    <em class="fa fa-pencil action-icon"></em>
                                                    <span class="text">{{$t('UpdateProgress')}}</span>
                                                </a>
                                                <a title="QuickView" data-placement="left" v-on:click="ProjectQuickView(item.PROJECT_ID,item.TASK_ID,item)" v-bind:value="item.PROJECT_ID" class="update actions-onclick" href="javascript:;" v-bind:attr="item.PROJECT_ID">
                                                    <em class="fa fa-eye text-info action-icon"></em>
                                                    <span class="text">{{$t('QuickView')}}</span>
                                                </a>
                                            </div>
                                        </div>
                                        <em class="fa fa-times close close-action_cv cursor-pointer" aria-hidden="true"></em>
                                    </span>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </draggable>
        </li>
    </ul>

    <div class="row" v-if="noRecordToDoList">
        <div class="col-md-12 col-lg-12">
            <div class="text-center text-danger d-flex align-items-center justify-content-center px-2 pt-0 py-4 no-record">
                {{$t('NoRecordfound')}}
            </div>
        </div>
    </div>
    <AddManualModal v-bind:manualData="trackerData" v-if="addManualShow" v-on:close="addManualShow = false" v-bind:store="store" v-on:closeUpdateModal="FetchData('kanban', '')" />
    <Comments v-bind:manualData="trackerData" v-if="commentsShow" v-on:close="commentsShow = false" v-bind:store="store" v-on:closeUpdateModal="FetchData('kanban', '')" />
    <RequestTimeModal v-bind:manualData="trackerData" v-if="requestShow" v-on:close="requestShow = false" v-bind:store="store" v-on:closeUpdateModal="FetchData('kanban', '')" />
    <Files v-if="ShowManageProjectQuickView" v-on:close="ShowManageProjectQuickView = false" v-bind:store="store" v-on:closeUpdateModal="FetchData('kanban', '')" />
    <AddNote v-if="showAddNoteModal" v-on:close="showAddNoteModal = false, modalNoteItem = null" v-bind:taskid="TaskId1" v-bind:store="store" v-on:closeUpdateModal="FetchData('kanban', '')">
        <h5 slot="header" class="modal-title">
            {{header}}
        </h5>
    </AddNote>
    <NoteListPopUp :userid="this.store.getUserInformation.id" reqfrom="dashboard" v-if="showNoteModal" v-on:close="showNoteModal = false, modalNoteTaskItem = null" v-bind:store="store">
        <h5 slot="header" class="modal-title">
            {{ $t('Notes') }}
        </h5>
    </NoteListPopUp>
    <UpdateProgress v-if="showUpdateProgressModal" v-on:close="showUpdateProgressModal = false" v-bind:store="store" v-on:closeUpdateModal="FetchData('kanban', '')" />

    <modaltracklocation v-if="showTrackLocationModal" v-on:close="showTrackLocationModal = false" v-bind:store="store">
        <h5 slot="header">{{ $t('Location') }}</h5>
    </modaltracklocation>

</div>
</template>

<script>
import DataService from '@/services/DataService';
import moment from 'moment';
import {
    formatter,
    convertSecondToHHMM,
    getSecondsFromDate
} from "@/assets/common/vue-common.js";
import AddManualModal from "@/components/TimeTrack/modals/AddManualModal.vue";
import Comments from "@/components/TimeTrack/modals/Comments.vue";
import RequestTimeModal from "@/components/TimeTrack/modals/RequestTimeModal.vue";

import Files from '@/components/Modal/todoListFiles.vue';
import AddNote from "@/components/Modal/modalAddNote.vue";
import UpdateProgress from '@/components/Modal/updateProgress.vue';
import modaltracklocation from "@/components/Modal/modaltracklocation.vue";

import NoteListPopUp from "@/components/Modal/modalNoteListPopUp.vue";
import {
    userTimerInfo
} from "@/stores/TimeTrack.js";
import { required } from 'vee-validate/dist/rules';
export default {
    name: "ToDo List Canban View",
    components: {
        AddManualModal,
        Comments,
        RequestTimeModal,
        Files,
        NoteListPopUp,
        AddNote,
        UpdateProgress,
        modaltracklocation,

    },
    props: {
        canban: {
            type: String
        },
        status:{
            type:String,
            required:false
        },
        module: {
          type: String,
          required: true,
        }
    },
    setup() {
        const store = userTimerInfo();
        return {
            store
        }
    },
    created() {
        this.FetchData();
        Object.assign(this.fromData, this.trackerData)
        this.store.getTimerInfo();
    },
    mounted() {
        const canbanScroll = document.querySelector('.onlyd-view');
        canbanScroll.addEventListener('scroll', e => {
            if (canbanScroll.scrollTop + canbanScroll.clientHeight >= canbanScroll.scrollHeight) {
                if (this.TotalPages > this.PageNumber) {
                    this.PageNumber = this.PageNumber + 1
                    this.FetchData('scroll')
                }
            }
        })

    },
    watch: {
        canban(newVal, oldVal) {
            this.FetchData('kanban', '');
        },

    },
    methods: {
        statusAccordingArray(text){
           if(this.status && this.status.length > 0  ){
           const isMatch = this.status.includes(text);
           if(isMatch){
             return isMatch?true:false
           }
        }
        else{
                    return true
                }
            // if(this.status && this.status.length > 0  ){
            // let obj = this.status.find((item)=> item==text)
            //     if(obj){
            //         obj=obj ? true : false
            //     }
            // return obj ? true : false
            // }else{
            //         return true
            //     }
        },
        OpenCommentModel(item) {
            var vm = this;
            Object.assign(vm.trackerData, vm.fromData)
            vm.trackerData.MESSAGE_BOARD_ID = item.MESSAGE_BOARD_ID
            vm.trackerData.totalHours = item.TRACKED_HOURS
            vm.trackerData.projectId = item.PROJECT_ID
            vm.trackerData.taskId = item.TASK_ID
            vm.trackerData.TASK_NAME = item.TASK_NAME
            vm.trackerData.trackDescription = item.TASK_DESCRIPTION
            this.commentsShow = true
        },
        RequestTimeBtn(item) {
            var vm = this;
            Object.assign(vm.trackerData, vm.fromData)
            vm.trackerData.MESSAGE_BOARD_ID = item.MESSAGE_BOARD_ID
            vm.trackerData.trackId = 0
            vm.trackerData.totalHours = item.TRACKED_HOURS
            vm.trackerData.projectId = item.PROJECT_ID
            vm.trackerData.taskId = item.TASK_ID
            vm.trackerData.TASK_NAME = item.TASK_NAME
            vm.trackerData.trackDescription = item.TASK_DESCRIPTION
            this.requestShow = true

        },
        automaticStart(item) {
            var vm = this;
            Object.assign(vm.trackerData, vm.fromData)
            vm.trackerData.startTime = moment().format("YYYY-MM-DD HH:mm:ss")
            vm.trackerData.stopTime = moment().format("YYYY-MM-DD HH:mm:ss")
            vm.trackerData.trackId = 0
            vm.trackerData.totalHours = 0
            vm.trackerData.projectId = item.PROJECT_ID
            vm.trackerData.taskId = item.TASK_ID
            vm.trackerData.trackDescription = item.TASK_DESCRIPTION
            if (this.store.getTimer.statusCode) {
                vm.confirmR(vm.$t("PreviouseStopNewRunTime"), true, (vm.$t("Send") + "  " + vm.$t("Records")), async function () {
                    await vm.timerStopOrPause('stop', 'TimeTrack');
                    if (vm.store.getTimerRunResponse.userMsg == 'stop') {
                        await vm.store.submitProjectInfo(vm.trackerData)
                        vm.FetchData()
                        vm.store.getTimerInfo();
                        vm.urlChange()
                    }

                })

            } else {
                vm.confirmR(vm.$t("ConfirmTimerContinue"), true, (vm.$t("Send") + "  " + vm.$t("Records")), async function () {
                    await vm.store.submitProjectInfo(vm.trackerData)
                    if (vm.store.getTimerRunResponse.trackId != 0 && vm.store.getTimerRunResponse.trackId) {
                        await vm.store.startTimer()
                        vm.FetchData()
                        vm.store.getTimerInfo();
                        vm.urlChange()
                    }

                })
            }

        },
        urlChange() {

            const query = Object.assign({}, this.$route.query);
            query.reload = 'reload'
            this.$router.replace({
                query
            });

        },
        ToggleActionButton(index) {
            this.isActiveActionButton = !this.isActiveActionButton;
            if (index === this.buttonIndex) {
                this.buttonIndex = ''; // Set the index if needed
            } else {
                this.buttonIndex = index
            }
        },
        manualyAddTime(item) {
            var vm = this;
            Object.assign(vm.trackerData, vm.fromData)
            vm.trackerData.trackId = 0
            vm.trackerData.totalHours = 0
            vm.trackerData.projectId = item.PROJECT_ID
            vm.trackerData.taskId = item.TASK_ID
            vm.trackerData.trackDescription = item.TASK_DESCRIPTION
            vm.trackerData.linkedTrackId = 0
            if (this.store.getTimer.statusCode) {
                vm.confirmR(vm.$t("AddManualWhenTimerAllreadyRunning"), true, (vm.$t("Send") + "  " + vm.$t("Records")), async function () {
                    await vm.timerStopOrPause('stop', 'TimeTrack');
                    if (vm.store.getTimerRunResponse.userMsg == 'stop') {
                        vm.addManualShow = true
                    }

                })

            } else {
                vm.addManualShow = true
            }
        },
        async timerStopOrPause(status, type) {
            this.store.getTimer.CreatedBy = this.store.getUserInformation.id
            this.store.getTimer.createdBy = this.store.getUserInformation.id
            this.store.getTimer.currentDate = moment().format('YYYY-MM-DD HH:mm:ss')
            this.store.getTimer.timerStatus = status
            this.store.getTimer.trackType = type
            this.store.getTimer.type = type
            this.store.trackAgent = "web"
            if (status == 'pause' && type == 'TimeTrack') {
                let countResumeInSeconds = 0
                this.store.$state.timerCountInSec = moment(new Date(this.store.getTimer.dateStop), "DD/MM/YYYY HH:mm:ss").format("DD-MM-YYYY") == "01-01-1950" ?
                    getSecondsFromDate({
                        startDate: this.store.getTimer.dateStart,
                        endDate: moment()
                    }, 'seconds') : countResumeInSeconds + parseInt(this.store.getTimer.resumeHours)
                this.store.getTimer.stopTime = moment().format('YYYY-MM-DD HH:mm:ss')
                this.store.getTimer.totalHours = this.store.$state.timerCountInSec
                await this.store.submitProjectInfo(this.store.getTimer)
                await this.store.getTimerInfo();
                this.store.stopTimer()
            } else if (status == 'stop' && type == 'TimeTrack') {
                this.store.getTimer.stopTime = moment().format('YYYY-MM-DD HH:mm:ss')
                this.store.getTimer.startTime = moment().format('YYYY-MM-DD HH:mm:ss')
                this.store.getTimer.totalHours = this.store.$state.timerCountInSec
                await this.store.submitProjectInfo(this.store.getTimer)
                await this.store.getTimerInfo();

                this.store.stopTimer()
                this.store.$state.timerCountInSec = 0
                this.store.$state.timerClock = "00:00:00"
            } else if (status == 'resume' && type == 'TimeTrack') {
                this.store.getTimer.startTime = moment().format('YYYY-MM-DD HH:mm:ss')
                await this.store.submitProjectInfo(this.store.getTimer)
                await this.store.getTimerInfo();
                this.store.$state.timerCountInSec = parseInt(this.store.getTimer.resumeHours)
                this.store.startTimer()
            }
            this.urlChange()

            this.FetchData()
            // else if(type=='Break'){
            //     this.trackerData.BreakStartTime = this.store.getTimer.dateStart
            //     this.trackerData.BreakDate= moment().format("YYYY-MM-DD HH:mm:ss")
            //     this.trackerData.BreakStopTime = moment().format('YYYY-MM-DD HH:mm:ss')
            //     let seconds = getSecondsFromDate({
            //         startDate: this.store.getTimer?.dateStart,
            //         endDate: moment()
            //     }, 'seconds')
            //     this.trackerData.totalHours = seconds
            //     await this.store.submitProjectInfo(this.trackerData)
            //     await this.store.getTimerInfo();
            //     this.store.breakStopTimer()
            //     if(this.store.timerInfo.statusCode=="PAUS" && this.store.timerInfo.trackType=='TimeTrack'){
            //       this.timerUpdateInfo('resume','TimeTrack')
            //     }
            // }
        },
        toggleWeeklyStatus() {
            //debugger
            if (!this.isActiveWeeklyStatus) {
                this.isActiveWeeklyStatus = true;
            } else {
                this.isActiveWeeklyStatus = false;
            }
        },

        showTrackPercent: function (item) {
            //debugger;
            var percent = 0;
            percent = (item.TRACKED_HOURS / ((item.ALLOCATE_HOURS) * 3600));
            percent = percent * 100;
            percent = parseFloat(percent, 10).toFixed(1);
            //if(percent==0)
            //{
            //    percent= item.ALLOCATE_HOURS * 3600;
            //}
            if (percent <= 0 || isNaN(percent) || percent.toLowerCase() == 'infinity') {
                percent = 100;
            } else {
                percent = 100 - percent;
            }
            if (Math.sign(percent) < 0) {
                percent = 0;
            }
            percent = Math.round(parseFloat(percent, 10));
            return percent;
        },
        formatOnlyMonthYearWithComma: function (value) {
            //debugger;
            if (value) {
                return moment(value).format('MMM') + ', ' + moment(value).format('YYYY');
            }
        },
        formatOnlyDate: function (value) {
            if (value) {
                return moment(value).format('DD');
            }
        },
        convertSecondsToHHMMSS: function (intSecondsToConvert) {

            var hours = this.convertHours(intSecondsToConvert);
            var minutes = this.getRemainingMinutes(intSecondsToConvert);
            minutes = minutes == 60 ? "00" : minutes;
            // var seconds = this.getRemainingSeconds(intSecondsToConvert);
            if (hours < 10) hours = "0" + hours;
            if (minutes < 10) minutes = "0" + minutes;
            //if (seconds < 10) seconds = "0" + seconds;
            return hours + ":" + minutes;
        },
        calculateWeekHoursAVG: function (totalHours, days) {
            var avgPerWeekHours = Math.floor(totalHours / days);
            if (isNaN(avgPerWeekHours)) {
                avgPerWeekHours = 0;
            }
            return avgPerWeekHours
        },
        convertSecondsToHHMMSSNew: function (intSecondsToConvert) {
            var hours = this.convertHours(intSecondsToConvert);
            var minutes = this.getRemainingMinutes(intSecondsToConvert);
            minutes = minutes == 60 ? "00" : minutes;
            var seconds = this.getRemainingSeconds(intSecondsToConvert);
            if (hours < 10) hours = "0" + hours;
            if (minutes < 10) minutes = "0" + minutes;
            if (seconds < 10) seconds = "0" + seconds;
            return hours + ":" + minutes + ":" + seconds;
        },
        convertHours: function (intSeconds) {
            var minutes = this.convertMinutes(intSeconds);
            var hours = Math.floor(minutes / this.minutesPerHour);
            return hours
        },
        convertHoursTwoDecimal: function (intSeconds) {

            var minutes = this.convertMinutes(intSeconds);
            var hours = (minutes / this.minutesPerHour).toFixed(2);
            return hours
        },
        convertMinutes: function (intSeconds) {
            return Math.floor(intSeconds / this.secondsPerMinute)
        },
        getRemainingMinutes: function (intSeconds) {
            var intTotalMinutes = this.convertMinutes(intSeconds);
            return intTotalMinutes % this.minutesPerHour
        },
        getRemainingSeconds: function (intTotalSeconds) {
            return intTotalSeconds % this.secondsPerMinute
        },
        splitTimeZone: function (item) {
            var fullname = item.split(' ');
            var sortname = item.split(')');
            return fullname[0];
        },
        splitTags: function (item) {
            return (item != null) ? item.split(",") : [];
        },
        convertSecondsToHH: function (intSecondsToConvert) {

            var hours = this.convertHoursTwoDecimal(intSecondsToConvert);
            if (hours < 10) hours = "0" + hours;
            return hours + " hrs "
        },

        async FetchData(e) {
            var vueObj = this;
            vueObj.isLoading = true;

            //if (history.pushState) {
            //    var historyUrl = "/Project/ToDoList";
            //    historyUrl = historyUrl + "?" + params;
            //    history.pushState({}, null, historyUrl);
            //}

            let keys = [];
            let values = [];
            if (e != 'scroll') {
                vueObj.cardViewData = []
                vueObj.toDoList = []
                vueObj.TagData = []
            }

            let url = ''
            // let queryString = new URLSearchParams(this.canban);
            // console.log("queryString",this.canban)
            // for (let pair of queryString.entries()) {
            //     if (pair[0] != 'pageNumber') {
            //         url += `&${pair[0]}=${pair[1]}`;
            //     }
            //     if (pair[0] == "PageSize") {
            //         this.PageSize = pair[1]
            //     }

            // }
            // url = `pageNumber=${this.PageNumber}${url}`
           
             url = `pageSize=${this.PageSize}&pageNumber=${this.PageNumber}&sortBy=${this.SortBy}&sortExp=${this.SortExp}&projectId=&type=kanban`;
            if(this.module){
                    url = url + "&moduleNameForSearch=" + this.module;
                    
                }
                else{
                    url = url + "&moduleNameForSearch=''";
                }
                console.log("status1",this.status)
                if(this.status){
                    // var data=''
                    // this.status.map((item,index)=>{
                    // data += index ==0 ? item : ','+item
                    // })
                    url = url + "&status=" + this.status;
                }
                else{
                url = url + "&status=";
                }
                
            await DataService.GetToDoListing(url).then(response => {

                if (response.data != null && response.data != "") {

                    //debugger;
                    vueObj.toDoList = response.data.result.DATA;
                    //vueObj.ModuleListSearch = response.data.result.MODULEDATA
                    vueObj.cardViewData.push.apply(vueObj.cardViewData, vueObj.toDoList)
                    vueObj.TotalPages = Math.ceil(vueObj.toDoList[0].TOTALRECORDS / vueObj.PageSize);
                    vueObj.CurrentPage = vueObj.PageNumber;
                    //vm.cardViewData=vm.toDoList;
                    vueObj.TagData = response.data.result.TAGDATA;
                    if (typeof vueObj.toDoList != 'undefined') {
                        if (vueObj.toDoList.length > 0) {
                            vueObj.TotalRecords = response.data.result.DATA[0].TOTALRECORDS;
                            vueObj.PendingRecords = response.data.result.DATA[0].PENDINGRECORDS;
                            vueObj.ProgressRecords = response.data.result.DATA[0].PROGRESSRECORDS;
                            vueObj.OverdueRecords = response.data.result.DATA[0].OVERDUERECORDS;
                            vueObj.UpcomingRecords = response.data.result.DATA[0].UPCOMINGRECORDS;
                            vueObj.CompletedRecords = response.data.result.DATA[0].COMPLETEDRECORDS;
                            //debugger;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            //debugger;
                            //vueObj.MaxCount = Math.max(vueObj.PendingRecords, vueObj.ProgressRecords, vueObj.OverdueRecords, vueObj.UpcomingRecords, vueObj.CompletedRecords);
                            //setTimeout(function () {
                            // ScrollKanbanView();
                            // }, 500);

                            vueObj.noRecordToDoList = false;

                        } else {
                            vueObj.TotalRecords = 0;
                            vueObj.ProgressRecords = 0;
                            vueObj.PendingRecords = 0;
                            vueObj.OverdueRecords = 0;
                            vueObj.UpcomingRecords = 0;
                            vueObj.CompletedRecords = 0;
                            vueObj.noRecordToDoList = true;

                        }
                    } else {
                        vueObj.TotalRecords = 0;
                        vueObj.ProgressRecords = 0;
                        vueObj.PendingRecords = 0;
                        vueObj.OverdueRecords = 0;
                        vueObj.UpcomingRecords = 0;
                        vueObj.CompletedRecords = 0;
                        vueObj.noRecordToDoList = true;
                    }
                    vueObj.isLoading = false;

                } else {
                    vueObj.TotalRecords = 0;
                    vueObj.ProgressRecords = 0;
                    vueObj.PendingRecords = 0;
                    vueObj.OverdueRecords = 0;
                    vueObj.UpcomingRecords = 0;
                    vueObj.CompletedRecords = 0;
                    vueObj.noRecordToDoList = true;
                    vueObj.isLoading = false;

                }
                //if(!vm.IsToDoView){
                //    setTimeout("ResponsiveDataTable('tablelistingdata')", 500);
                //}

            })

        },

        ChangeStatusDrag: function (evt, status) {
            var vm = this;
            var isChange = false;
            vm.isLoading = true;
            if (status != 2222) {
                if (typeof evt.added != 'undefined') {
                    if (evt.added.element.STATUS_ID == 1005 && status == vm.comData[0].STATUS_ID) {
                        isChange = true;
                    }
                    if (evt.added.element.STATUS_ID == 1005 && status == vm.inprogressData[0].STATUS_ID) {
                        isChange = true;
                    }
                    if (evt.added.element.STATUS_ID == vm.pendingData[0].STATUS_ID && status == vm.comData[0].STATUS_ID) {
                        isChange = true;
                    }
                    if (evt.added.element.STATUS_ID == vm.pendingData[0].STATUS_ID && status == vm.inprogressData[0].STATUS_ID) {
                        isChange = true;
                    }
                    if (evt.added.element.STATUS_ID == vm.inprogressData[0].STATUS_ID && status == vm.comData[0].STATUS_ID) {
                        isChange = true;
                    }
                    if (isChange) {
                        var VarObject = {
                            ID: evt.added.element.TASK_ID,
                            StatusId: status,
                            IsFor: 'TODOKANBANLISTING'
                        }
                        var objectPostString = JSON.stringify(VarObject);
                        // vm.$http.post(baseUrl + 'api/Common/CommonUpdateStatus', { postString: objectPostString },
                        DataService.UpdateStatus(objectPostString).then(function (response) {
                            // debugger;
                            //showLoader();
                            vm.isLoading = true;

                            if (response.data != null) {
                                if (response.data.status === 'Success') {
                                    vm.cardViewData = [];
                                    vm.PageNumber = 1;
                                    $("#hdfStartIndex").val(1);
                                    $("#hdfEndIndex").val(10);
                                    vm.FetchData('kanban', '');
                                    vm.ShowAlert(vm.$t("RecordUpdatedSuccessfully"), "success", true, vm.$t('Alert'));
                                } else {
                                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                                }
                            } else {
                                vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                            }
                            //hideLoader();
                            vm.isLoading = false;
                        });
                    } else {
                        vm.cardViewData = [];
                        vm.PageNumber = 1;
                        $("#hdfStartIndex").val(1);
                        $("#hdfEndIndex").val(10);
                        vm.FetchData('kanban', '');
                        vm.ShowAlert("Dragging between these two status is not allowed.", "warning", true, vm.$t('Alert'));
                        vm.isLoading = false;
                    }
                }
            } else {
                vm.cardViewData = [];
                vm.PageNumber = 1;
                $("#hdfStartIndex").val(1);
                $("#hdfEndIndex").val(10);
                vm.FetchData('kanban', '');
                vm.ShowAlert("Dragging not allowed in upcoming status.", "warning", true, vm.$t('Alert'));
            }
            vm.isLoading = false;
            // hideLoader();
        },
